<template>
    <div>
        <form class="form-signin" @submit.prevent="signin">
            <h1 class="h3 mb-3 font-weight-normal">請先登入</h1>
            <label for="inputEmail" class="sr-only">Email address</label>
            <input type="email" id="inputEmail" class="form-control" 
                placeholder="Email address" v-model="user.username" required autofocus>
            <label for="inputPassword" class="sr-only">Password</label>
            <input type="password" id="inputPassword" 
                class="form-control" v-model="user.password" placeholder="Password" required>
            <div class="checkbox mb-3">
                <label>
                    <input type="checkbox" value="remember-me"> Remember me
                </label>
            </div>
            <button class="btn btn-lg btn-primary btn-block" type="submit">Sign in</button>
            <p class="mt-5 mb-3 text-muted">&copy; 2017-2020</p>
        </form>
    </div>
</template>

<script>
//default export（預設匯出）
//default export 匯出時不需要預先賦予名稱，
//可以在 import 時另外賦予，但要特別注意 export default 每個檔案僅能有一個。
// 流程 使用v-model監控表單
// 表單送出觸發signin事件 執行登入API
export default {
  data () {
    return {
        user: {
            username: '',
            password: '',
        }
    }
  },
  methods:{
      signin(){
        //   登入 API 補充說明 (跨域) /admin
        const api = `${process.env.APIPATH}/admin/signin`;
        const vm = this;
        // 使用main.js載入的AJAX套件來載入AJAX
        // 登入API須丟入兩個參數 1.API路徑 2.帳號密碼物件
        this.$http.post(api, vm.user)
        .then((response) =>{
        console.log(response.data)
        // 如果登入成功 就把路徑改到首頁
        if (response.data.success){
          
            vm.$router.push('/admin/products');
        }
        });
      },
  },
}
</script>
<style scoped>
    html,
body {
  height: 100%;
}

body {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
  align-items: center;
  padding-top: 40px;
  padding-bottom: 40px;
  background-color: #f5f5f5;
}

.form-signin {
  width: 100%;
  max-width: 330px;
  padding: 15px;
  margin: auto;
}
.form-signin .checkbox {
  font-weight: 400;
}
.form-signin .form-control {
  position: relative;
  box-sizing: border-box;
  height: auto;
  padding: 10px;
  font-size: 16px;
}
.form-signin .form-control:focus {
  z-index: 2;
}
.form-signin input[type="email"] {
  margin-bottom: -1px;
  border-bottom-right-radius: 0;
  border-bottom-left-radius: 0;
}
.form-signin input[type="password"] {
  margin-bottom: 10px;
  border-top-left-radius: 0;
  border-top-right-radius: 0;
}
</style>


